.header,
.footer {
  background: #000 !important;
}

.konf-hero {
  background: #000;
  color: #fff;
  padding-bottom: 3rem;
}

.konf-hero-cta {
  align-items: center;
  border: 2px solid #f0c674;
  display: inline-flex;
  font-family: "SFMono-Regular", Consolas, "Liberation Mono", Menlo, Courier, monospace;
  font-size: .875rem;
  margin-right: 1.5rem;
  padding: .5rem 1.5rem;
  white-space: nowrap;
}

.konf-hero-cta svg {
  margin-right: .75rem;
}

.konf-hero-cta svg * {
  fill: currentColor;
}

.konf-hero-cta:hover {
  background: #f0c674;
  color: #000;
}

.konf-hero-layout {
  display: flex;
}

.konf-hero-text {
  flex-shrink: 0;
  margin-right: 3rem;
}

.konf-hero-image {
  margin-bottom: -6rem;
  display: none;
}

@media screen and (min-width: 60rem) {
  .konf-hero-image {
    display: block;
  }
}

.konf-hero-image img {
  box-shadow: rgba(0, 0, 0, 0.25) 0 5px 20px;
}

.konf-content {
  padding: 6rem 0 0;
}

.grid {
  display: grid;
  grid-gap: 3rem;
}

@media screen and (min-width: 45rem) {
  .grid {
    grid-template-columns: 1fr 1fr;
  }
}

@media screen and (min-width: 60rem) {
  .grid {
    grid-template-columns: 1fr 1fr 1fr;
  }

  .grid .column {
    grid-column: span var(--cols);
  }
}

.grid img {
  display: block;
  line-height: 0;
  margin-bottom: .75rem;
}

.konf-schedule .grid {
  grid-gap: 1.5rem;
  margin-bottom: 6rem;
}

.konf-schedule .column {
  background: #fff;
  padding: 1.5rem;
  border-radius: 3px;
}

.konf-schedule h3 {
  font-family: "SFMono-Regular", Consolas, "Liberation Mono", Menlo, Courier, monospace;
  font-size: .925rem;
  text-transform: uppercase;
  letter-spacing: 0.1em;
}

.konf-partners {
  display: flex;
  align-items: center;
  margin-bottom: 3rem;
}

.konf-partners a {
  margin-right: 3rem;
  display: flex;
  align-items: center;
  line-height: 0;
}

.konf-partners img {
  height: 4rem;
  width: auto;
}

.konf-tickets .tito-wrapper {
  background: #fff;
  border: 1px solid #fff;
  border-radius: 3px;
  margin: 0 !important;
  max-width: none !important;
}

.konf-tickets .tito-ticket-description {
  font-size: .875rem;
  color: #777;
}

.konf-tickets .tito-ticket.row {
  border-bottom: 2px solid #efefef !important;
}

.konf-tickets .tito-tickets-remaining {
  border: none !important;
  border-radius: 5rem;
  padding: .25rem .75rem !important;
}

.konf-tickets .tito-submit {
  background: #000 !important;
  border-color: #000 !important;
}

.konf-tickets .tito-submit:hover {
  background: #f0c674 !important;
  border-color: #f0c674 !important;
  color: #000 !important;
}

